@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body {
  font-family: 'Comfortaa', cursive;
}
#root {
  border-bottom: 3px solid rgb(187, 175, 12);
}

main {
  background-color: black;
  min-height: calc(100vh - 77px - 1.43vw);
}

@media (min-width: 1400px) {
  .body-container {
    max-width: 1000px;
  }
}

.body-container {
  color: rgb(128, 128, 128);
  margin: auto;
  height: 100%;
  padding-top: 5vh;
  min-height: 500px;
}

.panel {
  width: 100%;
}

.panel-header {
  margin-bottom: 10px;
}
.panel-header div {
  padding-left: 5px;
  padding-right: 5px;
  cursor: pointer;
}

.panel-header div:hover {
  color:antiquewhite
}

.panel-header .highlight {
  color:rgb(221, 207, 16)
}

.panel-header .highlight:hover {
  color:rgb(221, 207, 16)
}

.seperator {
  font-size: 0.87rem;
}

.panel-body {
  background-color: #292929;
  border-radius: 5px;
  height: 100%;
}

.footer-style {
  padding-top:20px;
  padding-bottom: 20px;
  background-color: black;
  color: white;
  font-size: 12px;
  text-align: center;
  vertical-align: center;
}

.header-padding {
  padding-top: 50px;
  /* border-left: 1px solid rgb(20,20,60);
  border-right: 1px solid rgb(20,20,60); */
}

.body-padding {
  padding-top: 20px;
}

.logo-assist-padding {
  padding-top: 20px;
  padding-left: 15px;
}

input {
  background-color: rgb(41,41,41);
  font-size: 15px;
  height: 40px;
  width: 90%;
  border: none;
  color: rgb(128, 128, 128);
  border-radius:10px;
  padding-left: 15px;
  outline: none;
}
form label {
  color: white;
}

.create-button {
  padding-left: 40px;
  padding-right: 40px;
  font-weight: bold;
  font-family: Poppins, sans-serif;
}

.connect-button {
  padding-left: 30px;
  padding-right: 30px;
  font-weight: bold;
  font-family: Poppins, sans-serif;
}

.font-bold-poppins {
  font-weight: bold;
  font-family: Poppins, sans-serif;
}

.spacing-medium {
  padding-top: 20px;
  padding-bottom: 5px;
}

.horizontal-spliter {
  height: 2px;
  width: 50%;
  margin: 10px 0px;
  background-color: rgb(242, 33, 255);
}

.nft-address-box {
  /* border-image: linear-gradient(-30deg, rgb(49, 94, 207), rgb(49, 94, 207)) 10 / 1 / 0 stretch; */
  border: 1px solid rgb(64,64,64);
  border-style: solid;
  display: -webkit-flex;
  display: flex;
  width: 70%;
  border-radius: 6px;
}

.nft-address-box input {
  text-align: center;
}

.nft-address-search {
  border: none;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  cursor: pointer;
  background: rgb(207, 177, 6);
  width: 10%;
  font-size: 15px;
}

.account-address-box {
  font-size: 16px;
  margin: 0px 0px 10px;
}

.select-account-address {
  display: inline-block;
  text-align: center;
  line-height: 1.4;
  font-size: 15px;
  min-height: 40px;
  width: 70%;
  background-color: rgb(41,41,41);
  border-radius: 6px;
  border: 1px solid rgb(64, 64, 64);
  padding-left: 8px;
  color: rgb(128, 128, 128); /* Firefox */
  -webkit-appearance:none; /* Safari and Chrome */
  appearance:none;
}


.top-text-group, .account-address-group, .nft-address-group, .mint-address-group {
  padding-bottom: 30px;
}

.black-back {
  background-color: black;
  text-align: end;
}

.transaction-gas-group {
  padding: 10px 20px;
  border: 1px solid lightslategray;
  border-radius: 10px;
  background-color: rgb(1, 15, 32);
}

.gas-button-group {
  width: 300px;
  padding-right: 10px;
}

.auto-button {
  padding: 0px 20px;
  margin-right: 15px;
}

.gas-price-limit-padding {
  padding-top: 10px;
}

.buy-nft-button {
  margin-top: 70px;
  font-size: 20px;
  font-family: Poppings, sans-serif;
}

.footerLine {
  display: block;
  width: 50vw;
  height: 0.13vw;
  background: #4f4dd4;
  opacity: .2;
  margin: 0 auto 1.3vw;
}

.timer-style {
  background-color: rgb(32, 34, 48);
  height: 55px;
  border: 2px solid rgb(32, 34, 48);
  border-radius: 10px;
  margin-left: 20px;
}

::-webkit-calendar-picker-indicator {
  -webkit-filter: invert(1);
          filter: invert(1);
}

.alert-style {
  width: 30%;
  position: fixed;
  top: 20px;
  right: 20px;
  display:inline;
}

.alert-close-style {
  cursor: pointer;
}

.font-size-1 {
  font-size: 12px;
}

.font-size-2 {
  font-size: 16px;
}

.font-size-3 {
  font-size: 24px
}

.title-1 {
  color: #f0d62b; 
  font-weight: 700;
}

.color-yellow {
  color: #f0d62b; 
}
.w-70 {
  width: 70%
}

.w-30 {
  width: 30%
}

.w-80 {
  width: 80%
}

.w-100 {
  width: 100%
}
.button {
  cursor: pointer;
}

.create-button {
  width: 70%;
  margin: auto;
  text-align: center;
  padding: 10px;
  background-color: #f0d62b;
  border-radius: 5px;
  margin-top: 10px;
  cursor: pointer;
}

.create-button:hover {
  background-color: #f5dc3d;
}

.auto-button {
  cursor: pointer;
}

.auto-button:hover p {
  color: white;
}

.wallet-button {
  padding: 10px;
  background-color: #f0d62b;
  border-radius: 5px;
  color: black
}

.wallet-button:hover {
  background-color: #f5dc3d;
}

.font-weight-bold {
  font-weight: bold;
}

.list-box {
  border: 1px solid rgb(64,64,64);
  border-radius: 6px;
}

.height-4 {
  min-height: 400px;;
}

.height-3 {
  min-height: 300px
}

.login-layout {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
